@font-face {
  font-family:"kentfont";
  src:url("@{fontdir}kentfont.eot");
  src:url("@{fontdir}kentfont.eot?#iefix") format("embedded-opentype"),
  url("@{fontdir}kentfont.woff") format("woff"),
  url("@{fontdir}kentfont.ttf") format("truetype"),
  url("@{fontdir}kentfont.svg?#kentfont") format("svg");
  font-weight:normal;
  font-style:normal;
}